<template>
  <div class="section">
    <el-table :data="list" border style="width: 100%">
      <template v-for="(item,i) in arr">
        <el-table-column v-if="item.child" :label="item.txt" :key="i">
          <el-table-column v-for="(l,j) in item.child" :prop="l.prop" :label="l.txt" :key="j"></el-table-column>
        </el-table-column>
        <el-table-column v-else :prop="item.prop" :label="item.txt" :key="i"></el-table-column>
      </template>
    </el-table>
    <div class="img">
      <img src="@/assets/well/1.png" alt="">
      <img src="@/assets/well/2.png" alt="">
      <img src="@/assets/well/3.png" alt="">
      <img src="@/assets/well/4.png" alt="">
      <img src="@/assets/well/5.png" alt="">
      <img src="@/assets/well/6.png" alt="">
    </div>
  </div>
</template>

<script>
import request from "@/utils/request";
import {wellhead} from '@/config/well'
export default {
  props: ["route"],
  data () {
    return {
      arr: wellhead,
      list: []
    }
  },
  mounted: function() {
    let data = this.route;
    request({
      url: '/data/query/wellheaddevicedata',
      method: 'post',
      data
    }).then(res => {
      this.list = res;
    });
  }
}
</script>
<style lang='scss' scoped>
.img {
  img {
    display: block;
    margin: auto;
  }
}
</style>


